<template>
    <div class="contract">
		<div class="mainBox">
			<div class="loginBox login" :style="{transform:turn?'rotateY(0)':'rotateY(180deg)'}">
				<div class="header">
					<div class="title">皓读小说</div>
					<span>遇见更好的自己</span>
				</div>
				<input type="text" placeholder="请输入您的注册手机号" v-model="loginForm.username">
				<input type="text" placeholder="请输入您的密码" v-model="loginForm.password">
				<div class="loginBtn" @click="toLogin()">登 陆</div>
				<div @click="turnChange()" class="pageToRegister">前往注册</div>
				<div class="forgetPassword">忘记密码</div>
			</div>
			<div class="loginBox register" :style="{transform:turn?'rotateY(180deg)':'rotateY(0)'}">
				<div class="header">
					<div class="title">皓读小说</div>
					<span>遇见更好的自己</span>
				</div>
				<input type="text" placeholder="请输入您要注册的手机号" v-model="registerForm.username">
				<input type="text" placeholder="请输入您的密码" v-model="registerForm.password">
				<div class="loginBtn" @click="toRegister()">注 册</div>
				<div @click="turnChange()" class="pageToLogin">前往登陆</div>
				<div class="forgetPassword">忘记密码</div>
			</div>
		</div>
    </div>
</template>

<script>
import {loginFn , registerFn} from '@/api/login'
export default {
  data() {
    return {
		turn: true,
		loginForm:{
			username:'',
			password:''
		},
		registerForm:{
			username:'',
			password:''
		},
    }
  },
  created() {
	// if(this.$store.state.user.hasLogin){
	//   	this.$router.go(-1)
	// }
    //this.getList()
    //this.register()
    
  },
  mounted() {
  	this.bgFn()
  },
  methods: {
	toLogin(){
		this.$store.dispatch('loginByUsername',this.loginForm).then(()=>{
			this.$notify.success({
				title:'登陆成功，欢迎回来'
			})
			this.$router.go(-1)
		})
	},
	toRegister(){
		const that = this;
		if(!(/^1[3456789]\d{9}$/.test(that.registerForm.username))) {
			this.$notify.error({
				title:'请输入正确的手机号'
			})
			return
		}else if(that.registerForm.password.length<6) {
			this.$notify.error({
				title:'密码过短,请输入6-8位密码'
			})
			return
		}else if(that.registerForm.password.length>8) {
			this.$notify.error({
				title:'密码过长,请输入6-8位密码'
			})
			return
		}
		registerFn(this.registerForm).then(res=>{
			that.$notify.success({
				title:'注册成功',
				message:'正在自动登陆'
			})
			this.loginForm = this.registerForm
			this.toLogin()
		})
	},
	
	
	
	turnChange(){
		this.turn = !this.turn
	},
	
	bgFn(){
		!function(){
		function n(n,e,t){
		return n.getAttribute(e)||t
		}
		function e(n){
		return document.querySelectorAll(n)
		}
		function t(){
		var t=e("script"),o=t.length,i=t[o-1];
		return{l:o,z:n(i,"zIndex",2),o:n(i,"opacity",1),c:n(i,"color","255,255,255"),n:n(i,"count",166)}
		}
		function o(){
		a=m.width=window.innerWidth||document.documentElement.clientWidth||document.body.clientWidth,
		c=m.height=window.innerHeight||document.documentElement.clientHeight||document.body.clientHeight
		}
		function i(){
		r.clearRect(0,0,a,c);
		var n,e,t,o,m,l;
		s.forEach(function(i,x){
		for(i.x+=i.xa,i.y+=i.ya,i.xa*=i.x>a||i.x<0?-1:1,i.ya*=i.y>c||i.y<0?-1:1,r.fillRect(i.x-.5,i.y-.5,1,1),e=x+1;e<u.length;e++)n=u[e],
		null!==n.x&&null!==n.y&&(o=i.x-n.x,m=i.y-n.y,
		l=o*o+m*m,l<n.max&&(n===y&&l>=n.max/2&&(i.x-=.03*o,i.y-=.03*m),
		t=(n.max-l)/n.max,r.beginPath(),r.lineWidth=t/2,r.strokeStyle="rgba("+d.c+","+(t+.2)+")",r.moveTo(i.x,i.y),r.lineTo(n.x,n.y),r.stroke()))
		}),
		x(i)
		}
		var a,c,u,m=document.createElement("canvas"),
		d=t(),l="c_n"+d.l,r=m.getContext("2d"),
		x=window.requestAnimationFrame||window.webkitRequestAnimationFrame||window.mozRequestAnimationFrame||window.oRequestAnimationFrame||window.msRequestAnimationFrame||
		function(n){
		window.setTimeout(n,1e3/45)
		},
		w=Math.random,y={x:null,y:null,max:2e4};m.id=l,m.style.cssText="position:fixed;top:0;left:0;z-index:"+d.z+";opacity:"+d.o,e(".contract")[0].appendChild(m),o(),window.onresize=o,
		window.onmousemove=function(n){
		n=n||window.event,y.x=n.clientX,y.y=n.clientY
		},
		window.onmouseout=function(){
		y.x=null,y.y=null
		};
		for(var s=[],f=0;d.n>f;f++){
		var h=w()*a,g=w()*c,v=2*w()-1,p=2*w()-1;s.push({x:h,y:g,xa:v,ya:p,max:6e3})
		}
		u=s.concat([y]),
		setTimeout(function(){i()},100)
		}();
	},
	
  },
 //  beforeDestroy() {
	// var contract = document.querySelector('.contract')
	// contract.removeChild("canvas")
 //  },
}
</script>

<style lang="scss" scoped>
	.contract {
		width: 100%;
		min-height: 100vh;
		background-color: rgb(40,40,41);
		padding-top: calc(50vh - 255px);
		.mainBox {
			overflow: hidden;
			z-index: 20;
			position: relative;
			width: 347px;
			height: 510px;
			margin: 0 auto;
			perspective: 1000rem;
		}
		.login {
			
		}
		.register {
			transform: rotateY(180deg);
		}
		.loginBox {
			transition: all 2s ease;
			backface-visibility: hidden;
			position: absolute;
			top: 0;
			left: 0;
			width: 347px;
			height: 510px;
			overflow: hidden;
			//box-shadow: inset 0 0 30px 10px rgb(234,67,54);
			// padding: 50upx;
			z-index: 30;
			border-radius: 6px;
			background-color: rgba(255,255,255,.9);
			padding: 60px 40px 0;
			.header {
				width: 100%;
				overflow: hidden;
				text-align: center;
				margin-bottom: 60px;
				.title {
					font-size: 20px;
					margin-bottom: 10px;
					letter-spacing: 1.5px;
					color: #000000;
				}
				span {
					font-size: 12px;
					color: rgb(168,168,168);
					letter-spacing: 0.5px;
				}
			}
			input {
				width: 100%;
				border-bottom: 1px solid #aaa;
				margin-bottom: 30px;
				height: 30px;
				font-size: 14px;
				padding-left: 10px;
				box-sizing: border-box;
			}
			.loginBtn {
				width: 100%;
				height: 40px;
				margin: 60px 0 20px 0;
				background-color: rgb(73,158,247);
				border-radius: 3px;
				line-height: 40px;
				color: #FFFFFF;
				font-size: 14px;
				cursor: pointer;
			}
			.pageToRegister, .pageToLogin, .forgetPassword {
				width: 50%;
				height: 20px;
				float: left;
				text-align: left;
				font-size: 12px;
				color: rgb(73,158,247);
				cursor: pointer;
			}
			.forgetPassword {
				text-align: right;
			}
		}
	}
	input {
		background:none;
		outline:none;
		border:none;
	}
</style>



